import React from "react";
import "./Widget.css";
import InfoIcon from "@mui/icons-material/Info";
import ProfileFollow from "./ProfileFollow";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";

function Widget() {
  return (
    <div className="widget__area">
      <div className="widget__feedheader">
        <h4>Add to your feed</h4>
        <InfoIcon style={{ color: "gray" }} sx={{ fontSize: 20 }} />
      </div>

      <div className="widget__feedbody">
        <ProfileFollow
          title="Solidity Devs"
          description="All about SOL & Blockchain TECH"
        />
        <ProfileFollow
          title="Virtual Computers"
          description="Ethereum Virtual machine lovers"
        />
        <ProfileFollow
          title="ChristLand Tech"
          description="Creations - Beyond Comprehension"
        />
      </div>
      <div className="widget__areafooter">
        <h5>View all recommendations </h5>
        <ArrowForwardIcon sx={{ fontSize: 20 }} />
      </div>
    </div>
  );
}

export default Widget;
