import Avatar from "@mui/material/Avatar";
import React from "react";
import "./Sidebar.css";
import Duke from "../src/assets/Duke_profile.png";

function Sidebar() {
  return (
    <div className="side__bar">
      <div className="sidebar__top">
        <img
          src="https://cdn.pixabay.com/photo/2016/04/26/15/01/holiday-1354563_960_720.jpg"
          alt=""
        />
        <Avatar
          className="sidebar__avatar"
          alt="/"
          src={Duke}
          sx={{ width: 60, height: 60 }}
        />
        <h2>Duke Ngboki</h2>
        <h4>Creative and Sound</h4>
      </div>

      <div className="sidebar__stats">
        <div className="sidebar__stat">
          <p>Who's viewed your profile</p>
          <p className="stat__number">4,262</p>
        </div>
        <div className="sidebar__stat">
          <p>Views on post</p>
          <p className="stat__number">3,892</p>
        </div>
      </div>
      <div className="sidebar__buttom">
        <p>Recent</p>
        <div className="sidebar__recentItems">
          <span className="sidebar__hash">#</span>
          <p>Solidity</p>
        </div>
        <div className="sidebar__recentItems">
          <span className="sidebar__hash">#</span>
          <p>EVM</p>
        </div>
        <div className="sidebar__recentItems">
          <span className="sidebar__hash">#</span>
          <p>Blockchain</p>
        </div>
      </div>
    </div>
  );
}

export default Sidebar;
