import React, { useState, useEffect } from "react";
import "./Feed.css";
import CreateIcon from "@mui/icons-material/Create";
import InputOption from "./InputOption";
import InsertPhotoIcon from "@mui/icons-material/InsertPhoto";
import SmartDisplayIcon from "@mui/icons-material/SmartDisplay";
import EventIcon from "@mui/icons-material/Event";
import NewspaperIcon from "@mui/icons-material/Newspaper";
import Post from "./Post";
import { db } from "./firebase";
import firebase from "./firebase";

function Feed() {
  const [input, setInput] = useState("");
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    db.collection("posts")
      .orderBy("timestamp", "desc")
      .onSnapshot((snapshot) =>
        setPosts(
          snapshot.docs.map((doc) => ({
            id: doc.id,
            data: doc.data(),
          }))
        )
      );
  }, []);

  const sendPost = (e) => {
    e.preventDefault();

    db.collection("posts").add({
      name: "Duke Ngboki",
      description: "Amaixin",
      message: input,
      photoUrl: "",
      timestamp: firebase.firestore.FieldValue.serverTimestamp(),
    });

    setInput("");
  };
  return (
    <div className="feed__area">
      <div className="feed__inputWrapper">
        <div className="feed__input">
          <CreateIcon />
          <form action="">
            <input
              type="text"
              value={input}
              onChange={(e) => setInput(e.target.value)}
            />
            <button onClick={sendPost} type="submit">
              Post
            </button>
          </form>
        </div>

        <div className="feed_inputOptions">
          <InputOption text="Photo" Icon={InsertPhotoIcon} color="#0a66c2" />
          <InputOption text="Video" Icon={SmartDisplayIcon} color="#A1CF4A" />
          <InputOption
            text="Event"
            Icon={EventIcon}
            color="rgb(237, 180, 73)"
          />
          <InputOption
            text="Article"
            Icon={NewspaperIcon}
            color="rgb(238, 106, 106)"
          />
        </div>
      </div>
      <div className="horizontal__line"></div>
      {posts.map(({ id, data: { name, description, message, photoUrl } }) => {
        return (
          <Post
            key={id}
            name={name}
            description={description}
            message={message}
            photoUrl={photoUrl}
          />
        );
      })}
      {/* <Post
        name="Duke Ngboki"
        description="Great Day"
        message="Testing the microphone"
        photoUrl="https://cdn.pixabay.com/photo/2017/09/25/13/12/cocker-spaniel-2785074_960_720.jpg"
      /> */}
    </div>
  );
}

export default Feed;
