import { Avatar } from "@mui/material";
import React from "react";
import InputOption from "./InputOption";
import "./Post.css";
import ThumbUpAltOutlinedIcon from "@mui/icons-material/ThumbUpAltOutlined";
import InsertCommentOutlinedIcon from "@mui/icons-material/InsertCommentOutlined";
import ShareIcon from "@mui/icons-material/Share";
import SendIcon from "@mui/icons-material/Send";

function Post({ name, description, message, photoUrl }) {
  return (
    <div className="post">
      <div className="post__header">
        <Avatar src={photoUrl} sx={{ width: 48, height: 48 }} />
        <div className="post__info">
          <h2>{name}</h2>
          <p>{description}</p>
        </div>
      </div>

      <div className="post__body">
        <p>{message}</p>
      </div>

      <div className="post__buttons">
        <InputOption Icon={ThumbUpAltOutlinedIcon} text="Like" color="gray" />
        <InputOption
          Icon={InsertCommentOutlinedIcon}
          text="Comment"
          color="gray"
        />
        <InputOption Icon={ShareIcon} text="Share" color="gray" />
        <InputOption Icon={SendIcon} text="Send" color="gray" />
      </div>
    </div>
  );
}

export default Post;
